import React from 'react'

import img from '../img/conan.jpg'

const Header = () => (
  <div className='mb2'>
    <img alt='conan' className='mb1 block' src={img} width='60' height='72' />
    <h1 className='mt0 h3 bold'>Conan’s monologue jokes (2010—2017)</h1>
  </div>
)

export default Header



